<template>
  <div class="big_box">
    <div class="scale_box">
      <span>当前房型:</span>
      <span>{{ orderAmend.roomScale.scaleName }}-{{ orderAmend.virtualRoomScale.scaleName }} {{ orderAmend.roomSn }}</span>
    </div>
    <div class="box_top_change">
      <div
        class="box_left"
        @click="btnScale"
      >
        <p style="color:#000;">基础房型</p>
        <span>{{ houseType }} ></span>
      </div>
      <div
        class="box_left"
        @click="btnPro"
      >
        <p style="color:#000;">产品房型</p>
        <span>{{ pro }} ></span>
      </div>
      <div
        class="bottom_left"
        @click="btnNum"
      >
        <p style="color:#000;">更换房号</p>
        <span>{{ num }} ></span>
      </div>
      <div
        class="bottom_left"
        @click="btnReason"
        v-if="orderType != 3"
      >
        <p style="color:#000;">换房原因</p>
        <span>{{ reason }} ></span>
      </div>
      <div style="height:20px;background:#f2f2f2;"></div>
      <div
        class="bottom_left"
        @click="isClean = !isClean"
      >
        <p style="color:#000;">原房间免打扫</p>
        <van-icon
          size='20px'
          :style="{'color':(isClean == true)?'#ff7d41':''}"
          :name="isClean == true?'checked':'circle'"
        />

      </div>
    </div>
    <!-- 确认换房 -->
    <div>
      <mt-button
        type="danger"
        v-if="orderType == 3 || orderType == 4"
        @click="btnOK"
        class="bottom_box"
      >{{isLifeCost != 0?'下一步':'确认换房'}}</mt-button>
      <mt-button
        type="danger"
        v-else
        @click="btnOK"
        class="bottom_box"
      >确认换房</mt-button>
    </div>
    <!-- 选择基础房型 -->
    <mt-popup
      v-model="scale_show"
      position="bottom"
      style="width:100%;position:fixed;bottom:0;height:70%;overflow:auto;"
    >
      <div style="width:100%; line-height:30px; font-size:0.8rem;">
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">当前可选基础房型</div>
        <div
          style="margin:0 20px; border-bottom:2px solid #f6f6f6; color:#f60;f"
          @click="chooseScale(item)"
          v-for="(item,i) in houselist"
          :key="i"
        >{{ item.scaleName }}</div>
      </div>
    </mt-popup>
    <!-- 选择产品房型 -->
    <mt-popup
      v-model="pro_show"
      position="bottom"
      style="width:100%;position:fixed;bottom:0;height:70%;overflow:auto;"
    >
      <div style="width:100%; line-height:30px; font-size:0.8rem;">
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">当前可选产品房型</div>
        <p
          style="margin:0 20px; border-bottom:2px solid #f6f6f6; color:#f60;"
          @click="choosePro(item)"
          v-for="(item,i) in prolist"
          :key="i"
        >{{ item.scaleName }}</p>
      </div>
    </mt-popup>
    <!-- 选择房号 -->
    <mt-popup
      v-model="num_show"
      position="bottom"
      style="width:100%;position:fixed;bottom:0;height:70%;overflow:auto;"
    >
      <div style="width:100%; line-height:30px; font-size:0.8rem;">
        <div style="font-size:15px;text-align:left;padding:12px 0 12px 20px;">当前可选房号</div>
        <div
          style="margin:0 20px; border-bottom:2px solid #f6f6f6; color:#f60;"
          @click="chooseNum(item)"
          v-for="(item,i) in numlist"
          :key="i"
        >{{ item.roomSn }}</div>
      </div>
    </mt-popup>
    <!-- 选择换房原因 -->
    <mt-popup
      v-model="renson_show"
      position="bottom"
      style="width:100%"
    >
      <div style="width:100%; box-sizing: border-box;">
        <p style="line-height:60px; font-size:0.8rem;text-align: left;text-indent: 1em;">
          <span>请选择换房原因</span>
        </p>
        <p
          class="p"
          v-for="(title,i) in reasons"
          :key="i"
          @click="chooseReason(title)"
        >{{ title }}</p>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { GetBusinessRoom, GetBusinessVirtualRoomscaleList, GetBusinessRoomscaleByhotel, PostBusinessOrderroomRoomchange } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            orderAmend: {},
            houseType: '',
            pro: '',
            num: '',
            reason: '',
            orderType: '',
            houselist: [],
            prolist: [],
            numlist: [],
            reasons: ['门锁无法打开', '房间设施损耗', '顾客要更换房型', '其他'],
            renson_show: false,
            scale_show: false,
            pro_show: false,
            num_show: false,
            isClean: false,
            isLifeCost: ''
        };
    },
    created() {
        this.orderAmend = JSON.parse(localStorage.getItem('item_amend'));
        this.isLifeCost = this.orderAmend.virtualRoomScale.isLifeCost;
        this.$store.commit('setHeader', '换房');
        this.houseType = this.orderAmend.roomScale.scaleName;
        this.houseType_id = this.oldSCacle_id = this.orderAmend.roomScaleId;
        this.pro = this.orderAmend.virtualRoomScale.scaleName;
        this.pro_id = this.oldPro_id = this.orderAmend.virtualRoomScaleId;
        this.num = this.orderAmend.roomSn;
        this.num_id = this.orderAmend.roomId;
        this.orderType = this.orderAmend.orderType;
    },
    methods: {
        // 基础房型
        btnScale() {
            this.scale_show = true;
            GetBusinessRoomscaleByhotel(this.orderAmend.hotelId).done((res) => {
                this.houselist = res.data.filter((item) => item.scaleName != this.orderAmend.roomScale.scaleName);
            });
        },
        chooseScale(item) {
            this.pro = '请选择';
            this.num = '请选择';
            this.houseType = item.scaleName;
            this.houseType_id = item.id;
            this.scale_show = false;
        },
        // 产品房型
        btnPro() {
            let param = {
                hotelId: this.orderAmend.hotelId,
                roomScaleId: this.houseType_id
            };
            GetBusinessVirtualRoomscaleList(param).done((res) => {
                if (this.orderAmend.virtualRoomScale.scaleType == '1' || this.orderAmend.virtualRoomScale.scaleType == '2') {
                    this.prolist = res.data.filter((item) => item.scaleType < 3);
                } else {
                    this.prolist = res.data.filter((item) => item.scaleType == this.orderAmend.virtualRoomScale.scaleType);
                }
                if (this.prolist.length > 0) {
                    this.pro_show = true;
                } else {
                    Toast({
                        message: '暂无房型可选',
                        duration: 900
                    });
                    return false;
                }
            });
        },
        choosePro(item) {
            this.num = '请选择';
            this.pro = item.scaleName;
            this.pro_id = item.id;
            this.pro_show = false;
        },
        // 房间号
        btnNum() {
            let data = {
                roomScaleId: this.houseType_id,
                status: '1'
            };
            GetBusinessRoom(data).done((res) => {
                if (res.data.length == 0) {
                    Toast({
                        message: '当前暂无其他房间可选',
                        duration: 900
                    });
                } else {
                    this.numlist = res.data;
                    this.num_show = true;
                }
            });
        },
        chooseNum(item) {
            this.num = item.roomSn;
            this.num_id = item.id;
            this.num_show = false;
        },
        // 换房原因
        btnReason() {
            this.renson_show = true;
        },
        chooseReason(item) {
            this.reason = item;
            this.renson_show = false;
        },
        // 确认换房
        btnOK() {
            if (this.pro == '请选择') {
                Toast({
                    message: '请选择产品房型',
                    duration: 900
                });
                return false;
            }
            if (this.num == '请选择' || this.num == this.orderAmend.roomSn) {
                Toast({
                    message: '请选择房间号',
                    duration: 900
                });
                return false;
            }
            let params = {
                orderId: this.orderAmend.orderId,
                toScaleId: this.houseType_id,
                toVirtualRoomScaleId: this.pro_id,
                toRoomId: this.num_id,
                clean: this.isClean == true ? '0' : '1'
            };
            if ((this.orderType == 3 && this.isLifeCost != 0) || (this.orderType == 4 && this.isLifeCost != 0)) {
                params.orderRoomId = this.orderAmend.id;
                this.$router.push({
                    path: '/changeRoom_long',
                    query: {
                        long_next: params
                    }
                });
                localStorage.setItem('long_next', JSON.stringify(params));
            } else {
                MessageBox.confirm('', {
                    message: '是否确定换房？',
                    title: '提示',
                    cancelButtonText: '否',
                    confirmButtonText: '是'
                })
                    .then((action) => {
                        if (action == 'confirm') {
                            PostBusinessOrderroomRoomchange(this.orderAmend.id, params).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                setTimeout('window.history.go(-1)', 900);
                            });
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        }
    }
};
</script>
<style scoped>
.scale_box {
    margin-top: 20px;
    text-align: left;
    margin-left: 20px;
    background: #f2f2f2;
}
.box_top_change {
    background-color: #fff;
    margin-top: 10px;
}
.box {
    border-bottom: 1px solid #f6f6f6;
    color: #000;
    line-height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.p {
    text-align: center;
    line-height: 35px;
    margin: 0 20px;
    border-bottom: 2px solid #f6f6f6;
    font-size: 0.8rem;
    color: #fb9e58;
}
.big_box {
    background-color: #f6f6f6;
    font-size: 15px;
    padding-bottom: 20px;
    position: fixed;
    top: 50px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
.box_left {
    margin: 0 20px;
    border-bottom: 1px solid #f6f6f6;
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 40px;
}
.bottom_left {
    margin: 0 20px;
    border-bottom: 1px solid #f6f6f6;
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 40px;
}
.bottom_box {
    width: 90%;
    height: 50px;
    margin-top: 30px;
    background-color: #fb9e58;
    font-size: 16px;
}
.c_ff {
    background: #ff7d41;
    width: 30px;
    height: 30px;
    border-radius: 20px;
}
</style>



